import { Component, OnInit, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef } from "@angular/core";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

import { RegionService } from "../shared/region/region.service";
import { LocationService } from "../shared/location/location.service";

import { Region } from "../shared/region/region";

@Component({
  selector: "app-region",
  templateUrl: "./region.component.html",
  styleUrls: ["./region.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RegionComponent implements OnInit {
  @ViewChild("displayCard") displayCard;
  @ViewChild("deleteCard") deleteCard;
  @ViewChild("createRegionModal") createRegionModal;
  @ViewChild("regionForm") regionForm;
  @ViewChild("body") body;

  public regionDetails: Region;
  public regionForDelete: Region;
  public regionForCreate: Region;
  public regionStates: { id: number; name: string }[];

  public regions: BehaviorSubject<Array<Region>>;
  public regionCount: number;
  public countries: BehaviorSubject<Array<any>>;
  public states: BehaviorSubject<Array<any>>;

  public selectedCountry: string;
  public selectedStates: Array<string>;
  public stateName = [];

  public searchQuery: string;
  public fetchDone: boolean;

  public countryActive: boolean;
  public StatesActive: boolean;

  constructor(
    public locationService: LocationService,
    public regionService: RegionService,
    private ref: ChangeDetectorRef
  ) {
    this.regionDetails = new Region();
    this.regionForDelete = new Region();
    this.regionForCreate = new Region();
    this.regionCount = 0;

    this.regions = this.regionService.regions;
    this.countries = this.locationService.countries;
    this.states = this.locationService.states;
    this.fetchDone = false;

    this.countryActive = false;
    this.StatesActive = false;
  }

  ngOnInit() {
    this.regionService.getRegions().subscribe();
    this.regions.subscribe(data => {
      this.regionCount = data.length;
    });
    this.states.subscribe(data => {
      const stateIds = [];

      this.regionForCreate.states.forEach(state => {
        data.forEach(v => {
          if (state.name.toLowerCase() === v.label.toLowerCase()) {
            stateIds.push(v.value);
          }
        });
      });
      this.onStatesChange(stateIds);
    });
  }

  onCountryChange(countryId) {
    if (countryId) {
      this.selectedCountry = countryId;

      this.locationService.getStates(this.selectedCountry);

      this.countries.value.forEach(v => {
        if (v.value === this.selectedCountry) {
          this.regionForCreate.country = v.label;
        }
      });
    }
  }

  onStatesChange(stateIds) {
    if (stateIds && stateIds.length > 0) {
      const states = [];

      stateIds.forEach(v => {
        return this.states.value.forEach(p => {
          if (v === p.value) {
            const val = { id: p.value, name: p.label };
            states.push(val);
          }
        });
      });

      this.selectedStates = stateIds;
      this.regionForCreate.states = states;
      this.ref.detectChanges();
    }
  }

  promptRegionDelete(region: Region) {
    this.deleteCard.show();
    this.regionForDelete = region;
  }

  removeRegion(region: Region) {
    this.displayCard.hide();
    this.deleteCard.hide();
    this.regionService.deleteRegion(region);
  }

  showRegionDetail(region: Region) {
    this.stateName = [];
    this.regionDetails = region;
    this.regionDetails.states.forEach(data => {
      this.stateName.push(data.name);
    });
    this.displayCard.show();
  }

  submitRegion() {
    this.regionService.createOrUpdate(this.regionForCreate);
    this.createRegionModal.hide();
  }

  updateRegion(region: Region) {
    this.regionForCreate = Object.assign(new Region(), region);

    this.countries.value.forEach(v => {
      if (v.label.toLowerCase() === region.country.toLowerCase()) {
        this.onCountryChange(v.value);
      }
    });

    this.createRegionModal.show();
  }

  openCreateModal() {
    this.regionForCreate = new Region();
    this.selectedCountry = "";
    this.selectedStates = [];
    this.regionForm.reset();
    this.createRegionModal.show();
  }

  searchValueChange(q) {
    this.searchQuery = q;
    this.regionService.searchTerm.next(this.searchQuery);
  }
}



